.header-menu-item{display:flex;height:60%;min-width:10%;padding:0.2rem 1rem;justify-content:flex-start;align-items:center;border-right:0px solid black;border-left:1px solid #1e2631}.header-menu-item__text{color:#1e2631;font-size:0.9rem;font-weight:700;text-decoration:none}.header-menu-item__select{border-bottom:0.15rem solid #1e2631;line-height:25px}.header-menu-item__fist{border-left:none}.header-menu-item__icon{height:1.5rem;margin-right:0.5rem}

.header-menu{height:100%;margin-bottom:0px;padding-top:0rem;padding-left:0px;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;list-style-type:none}

.logo{height:2.4rem;display:flex;align-items:center;justify-content:center}.logo__img-wallet{height:2rem;margin-right:0.2rem}.logo__img-text{height:1.3rem}.logo__img4u{margin-left:0.1rem;height:1.3rem}

.header{position:fixed;width:100%;height:3.5rem;margin-bottom:0.4rem;padding-top:0.5rem;background-color:#fff;box-shadow:#0a3c5d 0px 7px 8px -8px;z-index:999;display:flex;justify-content:space-between;flex-wrap:nowrap;align-items:center}.header__logo{display:flex;width:auto;height:100%;margin-left:1rem;padding-bottom:0.5rem;justify-content:flex-start;align-items:center;flex:0 1 auto}.header__menu{height:100%;margin-bottom:0.2rem}.header__config{display:flex;margin-right:1rem;align-items:center}

.container-main-page{position:relative;display:flex;width:100%;height:auto;overflow-y:scroll;overflow-x:hidden;margin-bottom:1rem;margin-left:0.2rem;padding-top:3.5rem;padding-right:0px;flex-direction:row;justify-content:center;align-self:auto;order:1;flex:0 1 auto;object-fit:fill}.container-main-page::-webkit-scrollbar{width:0.8rem}.container-main-page::-webkit-scrollbar-thumb{background:#c5c5c5}

.container-aside-panel{overflow:auto;width:19rem;margin-top:3.5rem;padding-top:2rem;padding-right:0.5rem;padding-left:0.5rem;background-color:#ffffff;box-shadow:#0a3c5d 0px 0px 8px -4px}.container-aside-panel::-webkit-scrollbar{width:0.6rem}.container-aside-panel::-webkit-scrollbar-thumb{background:#c5c5c5}

.aside-menu-item{margin-top:0.8rem;margin-bottom:0.8rem;margin-left:0rem;padding-top:0.3rem;padding-bottom:0.3rem;padding-left:1.2rem;list-style-type:none;color:#1e2631}.aside-menu-item__select{padding-left:1.1rem;border-left:3px solid #0a3c5d;background-color:#f8f8f8;color:#0a3c5d}.aside-menu-item__percentage{margin-bottom:0.3rem;margin-left:0.3rem;font-size:0.9rem;font-weight:600}.aside-menu-item__name{margin-bottom:0.3rem;padding-left:0.6rem;font-size:0.9rem;font-weight:600}

.select-wallet{margin-top:1rem;margin-left:0.5rem}.select-wallet__title{display:flex;align-items:center;font-size:1rem;font-weight:700}.select-wallet__icon{width:1rem;height:auto}.select-wallet__text{padding-left:0.3rem;color:#1e2631;font-size:1rem}.select-wallet__list{list-style-type:none}

.select-wallet-to-balance{margin-top:1rem;margin-left:0.5rem}.select-wallet-to-balance__list{list-style-type:none}

.aside-button{height:1.8rem;background:transparent;border:none;margin-left:2.2rem;display:flex;flex-direction:row;align-items:center}.aside-button__icon{height:0.9rem;max-width:none}.aside-button__text{margin-left:0.5rem;color:#1e2631;font-size:0.9rem;font-weight:600}

.aside-menu-wallet{width:100%;display:flex;flex-direction:column;justify-content:flex-start}.aside-menu-wallet__container{margin-bottom:1rem}.aside-menu-wallet__title{margin-left:0.3rem;color:#1e2631;font-size:1.2rem;font-weight:700}

.alert-to-confirm{position:absolute;padding:5%;width:100%;height:92%;z-index:99;background-color:rgba(0,0,0,0.5);-webkit-tap-highlight-color:transparent}

.cards-investment{width:100%;margin-top:1.8rem;margin-bottom:3rem;display:flex;justify-content:center;align-items:center}.cards-investment__item{margin-right:0rem;margin-left:0rem;flex-flow:column wrap;justify-content:center;align-items:center}.cards-investment__result{width:33%;display:flex;justify-content:flex-end;align-items:center}.cards-investment__total{margin-right:3.5rem;margin-left:3.5rem}.cards-investment__cost{width:33%;display:flex;justify-content:flex-start;align-items:center}.cards-investment__value{display:inline;color:#0a3c5d;font-size:1.5rem;font-weight:600}.cards-investment__percent{color:#0a3c5d;display:inline;padding-left:0.5rem;font-size:1rem;font-weight:600}.cards-investment__legend{color:#0a3c5d;margin-top:0.3rem;font-size:0.8rem;text-align:center}.cards-investment__value--main{color:#087233;font-size:2.2rem;font-weight:600}.cards-investment__legend--main{color:#087233}

.investment-card{width:100%;border-radius:0.8rem;margin-bottom:0.8rem;background-color:#fcfcfc;box-shadow:black 1px 1px 10px -8px;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}

.radial-chart{width:auto}

.header-investment-card{position:relative;display:flex;width:100%;height:7.8rem;margin-top:0.3rem;margin-left:0px;padding-top:0.5rem;padding-bottom:0.5rem;justify-content:center;align-items:center;border-radius:0rem;background-color:#fcfcfc;box-shadow:none}.header-investment-card__right{display:flex;width:40%;flex-direction:column;justify-content:center;align-items:center}.header-investment-card__left{width:40%;padding-left:4.5rem}.header-investment-card__name{margin-bottom:0.5rem;color:#074b7e;font-size:1.5rem;font-weight:600}.header-investment-card__value{display:inline;color:#4989b9;font-size:1.2rem;font-weight:500}.header-investment-card__porcentage{display:inline;color:#4989b9;font-size:1rem;font-weight:500;margin-left:0.3rem}.header-investment-card__container{margin-bottom:0.5rem}.header-investment-card__button{background-color:transparent;position:absolute;bottom:0.5rem;right:0.5rem;width:auto;height:1rem;margin:0.5rem;border:none}.header-investment-card__icon{height:1rem}

