.filter-by-date{width:100%}.filter-by-date__date-pickers{display:flex;justify-content:center;align-items:center;width:100%}

.earnings{position:relative;margin-top:2rem;width:100%;height:50%;background:#F8F8F8;display:flex;flex-flow:column nowrap;align-items:center}

.counter-percentage{display:flex;align-items:center}.counter-percentage__button{margin-bottom:0.2rem;color:#fff;border:none;border-radius:4px;width:1.5rem;height:1.5rem;font-size:1.2rem;display:flex;justify-content:center;align-items:center;cursor:pointer}.counter-percentage__button:hover{-webkit-filter:brightness(115%);filter:brightness(115%)}.counter-percentage__value{margin-right:0.3rem;margin-left:0.3rem;font-weight:600}.counter-percentage__input{background-color:transparent;font-weight:600;border:none;border-bottom:1px solid;text-align:center}textarea:focus,input:focus{box-shadow:0 0 0 0;outline:0}

.table-row-for-view{width:100%;min-height:3rem;padding:0.5rem 1rem 0.5rem 0rem;display:flex;justify-content:flex-start;align-items:center;border-bottom:0.1rem solid black}.table-row-for-view--color{background-color:#feffea}.table-row-for-view__text{width:20%;margin:0rem 0.5rem}.table-row-for-view__button{width:5rem}

.MuiInput-underline:before{border-bottom:none !important}

.editable-table-row{background-color:#def7e3;width:100%;min-height:3rem;padding:0.5rem 1rem 0.5rem 0rem;display:flex;justify-content:flex-start;align-items:center;border-bottom:0.1rem solid black}.editable-table-row__text{width:20%;margin:0rem 0.5rem}.editable-table-row__loading{margin:0.5rem;width:3rem}.editable-table-row__button{width:5rem;margin:0rem 0.5rem}

.total-wallet{display:flex;flex-direction:column;align-items:center}.total-wallet__container{width:18rem;display:flex;justify-content:space-between}.total-wallet__text{font-size:1.6rem;font-weight:700}.total-wallet__value{font-size:1.6rem;font-weight:700}.total-wallet__text--red{color:#c70e50}.total-wallet__alert{font-size:1rem;font-weight:700;color:#c70e50;margin:0.3rem 1.3rem}.total-wallet--margin{margin-bottom:2rem}

.table-wallets-customize{width:100%;height:75%;position:relative;padding:0.5rem 5% 0.5rem 5%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;border-radius:1.2rem;background-color:#fcfcfc;box-shadow:black 1px 1px 10px -8px}.table-wallets-customize__title{color:#2b86bd;font-size:1.8rem;font-weight:700}.table-wallets-customize__header{width:100%;display:flex;justify-content:space-between;margin:1rem 0;align-items:center}.table-wallets-customize__columns{width:100%;height:3rem;padding:0.5rem 5rem 0.5rem 0rem;display:flex;justify-content:flex-start;align-items:center;border-bottom:0.1rem solid black}.table-wallets-customize__rows{width:100%;max-height:100%;overflow-y:scroll}.table-wallets-customize__rows::-webkit-scrollbar{width:0.5rem}.table-wallets-customize__rows::-webkit-scrollbar-thumb{background:#c5c5c5}.table-wallets-customize__cell{width:20%;margin:0rem 0.5rem;font-weight:600}.table-wallets-customize__footer{width:100%;height:4rem;margin-top:0.3rem;padding-right:17%;display:flex;justify-content:flex-end}

.input-text{margin:0 1rem}.input-text__name{width:100%;font-size:0.8rem;color:#436175;font-weight:700}.input-text__input{background-color:transparent;font-size:1.8rem;width:16rem;color:#436175;font-weight:700;border:none;border-bottom:1px solid #436175}textarea:focus,input:focus{box-shadow:0 0 0 0;outline:0}

.container-title-editable{display:flex;align-items:flex-end;margin-bottom:0.2rem}.container-title-editable__text{margin-left:0.5rem;color:#328bc3;font-size:1.5rem;font-weight:600}

.wallets-customize{position:relative;width:100%;margin-top:0.5rem;padding:1rem 5% 0.3rem 5%;border-radius:0px;background-color:#f8f9fa}.wallets-customize__header{position:relative;height:4rem;margin-top:0.6rem;margin-bottom:1rem;display:flex;justify-content:center;align-items:center}.wallets-customize__icon{position:Absolute;left:0;width:4rem}.wallets-customize__buttons{width:100%;margin-top:1.5rem;margin-bottom:2rem;padding:0 10rem;display:flex;justify-content:space-around;align-items:center}.wallets-customize__alert{position:Absolute;top:0;left:5%;width:90%}

.page-wallets{width:100vw;height:99vh;margin-bottom:0.2rem;display:flex;justify-content:center}.page-wallets__container{width:100%;height:100%;background-color:#F8F8F8;margin-left:0rem;display:flex;align-items:flex-start}.page-wallets__loading{position:absolute;top:10rem;width:7rem}

