.wallets-customize{
    position: relative;
    width: 100%;
    margin-top: 0.5rem;
    padding: 1rem 5% 0.3rem 5%;
    border-radius: 0px;
    background-color: rgb(248, 249, 250);
}

.wallets-customize__header{
    position: relative;
    height: 4rem;
    margin-top: 0.6rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wallets-customize__icon{
    position: Absolute;
    left:0;
    width: 4rem;
}

.wallets-customize__buttons{
    width: 100%;
    margin-top: 1.5rem;
    margin-bottom: 2rem;
    padding: 0 10rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.wallets-customize__alert{
    position: Absolute;
    top:0;
    left:5%;
    width: 90%;
}